"use client";

import { useEffect, useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { useRouter } from "next/navigation";
import Link from "next/link";
import { useAuthStore } from "../store/authStore";
import { useProfileStore } from "../store/profileStore";
import { profileApi, problemsApi, graphApi, reviewApi } from "../lib/api";
import type { Profile } from "../lib/types";
import {
    Brain, GitBranch, BookOpen, ArrowRight, TrendingUp, TrendingDown,
    Zap, Star, AlertTriangle, RefreshCw, ChevronDown, ChevronUp,
    Target, Sparkles, Clock, BarChart2, X
} from "lucide-react";

// ‚îÄ‚îÄ‚îÄ Helpers ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ

const DIFF_LABELS = ["", "Easy", "Easy-Med", "Medium", "Hard", "Expert"];
const DIFF_COLORS = ["", "text-green-600", "text-lime-600", "text-yellow-600", "text-red-500", "text-purple-500"];
const DIFF_BG = ["", "bg-green-50", "bg-lime-50", "bg-yellow-50", "bg-red-50", "bg-purple-50"];

const EXP_BADGE: Record<string, string> = {
    beginner: "bg-emerald-100 text-emerald-700 border border-emerald-200",
    some_basics: "bg-emerald-100 text-emerald-700 border border-emerald-200",
    intermediate: "bg-amber-100 text-amber-700 border border-amber-200",
    advanced: "bg-purple-100 text-purple-700 border border-purple-200",
    complete_beginner: "bg-emerald-100 text-emerald-700 border border-emerald-200",
};

const THINKING_ICON: Record<string, string> = {
    brute_force: "üî®",
    optimized: "‚ö°",
    pattern_matching: "üîç",
    confused: "ü§î",
    creative: "‚ú®",
    methodical: "üìê",
    unknown: "üß†",
};

// Runs only client-side to avoid SSR UTC vs local-time mismatch
function getGreeting(): string {
    const h = new Date().getHours();
    if (h < 5) return "Working late";
    if (h < 12) return "Good morning";
    if (h < 17) return "Good afternoon";
    return "Good evening";
}

// ‚îÄ‚îÄ‚îÄ Expandable card ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ

function ExpandableCard({
    icon: Icon,
    color,
    bg,
    count,
    label,
    sublabel,
    items,
    emptyText,
    itemColor = "text-[#374151]",
    badgeClass = "bg-[#f3f4f6] text-[#374151]",
}: {
    icon: React.ElementType;
    color: string;
    bg: string;
    count: number;
    label: string;
    sublabel?: string;
    items: string[];
    emptyText: string;
    itemColor?: string;
    badgeClass?: string;
}) {
    const [open, setOpen] = useState(false);
    return (
        <div className="bg-white border border-[#e5e7eb] rounded-2xl overflow-hidden">
            <button
                onClick={() => setOpen(!open)}
                className="w-full p-5 flex items-center justify-between hover:bg-[#fafafa] transition-colors"
            >
                <div className="flex items-center gap-3">
                    <div className={`w-10 h-10 ${bg} rounded-xl flex items-center justify-center`}>
                        <Icon className={`w-5 h-5 ${color}`} />
                    </div>
                    <div className="text-left">
                        <div className="font-serif font-bold text-2xl text-[#0d0d0d] leading-none">{count}</div>
                        <div className="text-xs text-[#6b7280] mt-0.5">{label}</div>
                    </div>
                </div>
                <div className="flex items-center gap-2">
                    {sublabel && <span className="text-xs text-[#9ca3af] hidden sm:block">{sublabel}</span>}
                    {open
                        ? <ChevronUp className="w-4 h-4 text-[#9ca3af]" />
                        : <ChevronDown className="w-4 h-4 text-[#9ca3af]" />
                    }
                </div>
            </button>
            <AnimatePresence>
                {open && (
                    <motion.div
                        initial={{ height: 0, opacity: 0 }}
                        animate={{ height: "auto", opacity: 1 }}
                        exit={{ height: 0, opacity: 0 }}
                        transition={{ duration: 0.25 }}
                        className="overflow-hidden"
                    >
                        <div className="px-5 pb-5 pt-1 border-t border-[#f3f4f6]">
                            {items.length === 0 ? (
                                <p className="text-sm text-[#9ca3af] py-2">{emptyText}</p>
                            ) : (
                                <div className="flex flex-wrap gap-2 mt-2">
                                    {items.map((item) => (
                                        <span
                                            key={item}
                                            className={`text-xs font-medium px-2.5 py-1 rounded-full ${badgeClass}`}
                                        >
                                            {item}
                                        </span>
                                    ))}
                                </div>
                            )}
                        </div>
                    </motion.div>
                )}
            </AnimatePresence>
        </div>
    );
}

// ‚îÄ‚îÄ‚îÄ Main Page ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ

export default function DashboardPage() {
    const router = useRouter();
    const { isAuthenticated, user } = useAuthStore();
    const { profile, setProfile } = useProfileStore();

    const [recommendations, setRecommendations] = useState<any[]>([]);
    const [solvedHistory, setSolvedHistory] = useState<any[]>([]);
    const [upcomingProblems, setUpcomingProblems] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);
    const [greeting, setGreeting] = useState("");

    // Calculate greeting client-side only to respect browser local time
    useEffect(() => { setGreeting(getGreeting()); }, []);

    useEffect(() => {
        if (!isAuthenticated) { router.push("/login"); return; }
        (async () => {
            try {
                const [profileRes, reviewRes, recsRes, problemsRes] = await Promise.all([
                    profileApi.get(),
                    reviewApi.history().catch(() => ({ data: [] })),
                    graphApi.recommendations().catch(() => ({ data: [] })),
                    problemsApi.list().catch(() => ({ data: [] })),
                ]);

                setProfile(profileRes.data);
                setSolvedHistory(reviewRes.data || []);

                const recs: any[] = recsRes.data || [];
                setRecommendations(recs);

                // Map Neo4j recommendations (concept names) to problems
                const problems: any[] = problemsRes.data || [];
                if (recs.length > 0) {
                    const recConceptNames = new Set(recs.map((r: any) => r.name?.toLowerCase()));
                    const matched = problems.filter((p: any) =>
                        (p.concept_ids || []).some((c: string) => recConceptNames.has(c.toLowerCase()))
                    );
                    setUpcomingProblems(matched.slice(0, 6));
                } else {
                    // fallback: just show first 6 problems
                    setUpcomingProblems(problems.slice(0, 6));
                }
            } catch {
                // silently handle
            } finally {
                setLoading(false);
            }
        })();
    }, [isAuthenticated, router, setProfile]);

    if (loading) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-[#faf9f6] pt-16">
                <div className="text-center">
                    <div className="w-10 h-10 border-2 border-[#f97316] border-t-transparent rounded-full animate-spin mx-auto mb-4" />
                    <p className="text-[#6b7280] text-sm">Loading your profile‚Ä¶</p>
                </div>
            </div>
        );
    }

    // Prefer freshly-fetched profile name; fall back to auth store (set at login)
    const p = profile as any;
    const firstName = (user?.name || p?.name || "").split(" ")[0] || "";
    const expLevel = profile?.experience_level || "beginner";
    const thinkStyle = p?.thinking_style || "unknown";
    const goal = p?.goal || "";
    const gaps = profile?.gaps || [];
    const strengths = profile?.strengths || [];
    const toRevise = p?.topics_to_revise || p?.recent_weaknesses || [];
    const knownConcepts = p?.known_concepts || Object.keys(profile?.skills || {});
    const submissionsCount = profile?.submissions_count || 0;

    // Unique solved problem titles from review history
    const solvedTitles = Array.from(new Set(solvedHistory.map((r: any) => r.problem_title).filter(Boolean)));

    return (
        <div className="min-h-screen bg-[#faf9f6] pt-20">
            <div className="max-w-6xl mx-auto px-6 py-10">

                {/* ‚îÄ‚îÄ GREETING ‚îÄ‚îÄ */}
                <motion.div
                    className="mb-10"
                    initial={{ opacity: 0, y: 18 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.5 }}
                >
                    {/* Greeting ‚Äî only renders after client mount so time is correct */}
                    {greeting && (
                        <p className="text-[#9ca3af] text-sm tracking-wide mb-1">{greeting},</p>
                    )}
                    <h1 className="font-serif font-bold text-4xl md:text-5xl text-[#0d0d0d] leading-tight">
                        {firstName || user?.email?.split("@")[0]}
                        <span className="text-[#f97316]">.</span>
                    </h1>

                    {/* Profile pills */}
                    <div className="flex flex-wrap items-center gap-2 mt-4">
                        <span className={`text-xs font-semibold px-2.5 py-1 rounded-full ${EXP_BADGE[expLevel] || EXP_BADGE.beginner}`}>
                            {expLevel.replace(/_/g, " ")}
                        </span>
                        <span className="text-xs bg-[#f3f4f6] text-[#6b7280] border border-[#e5e7eb] px-2.5 py-1 rounded-full">
                            {THINKING_ICON[thinkStyle]} {thinkStyle.replace(/_/g, " ")} thinker
                        </span>
                    </div>

                    {/* Goal ‚Äî shown as a clean subtitle, not a cramped pill */}
                    {goal && (
                        <p className="mt-3 text-sm text-[#6b7280] flex items-center gap-1.5">
                            <Target className="w-3.5 h-3.5 text-[#f97316] shrink-0" />
                            <em>{goal}</em>
                        </p>
                    )}
                </motion.div>

                {/* ‚îÄ‚îÄ PROFILE STAT CARDS (expandable) ‚îÄ‚îÄ */}
                <motion.div
                    className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4 mb-8"
                    initial={{ opacity: 0, y: 18 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.5, delay: 0.1 }}
                >
                    {/* Submissions ‚Äî not expandable, just a link */}
                    <Link href="/profile" className="no-underline">
                        <div className="bg-white border border-[#e5e7eb] rounded-2xl p-5 hover:border-[#f97316]/40 transition-all card-hover h-full">
                            <div className="w-10 h-10 bg-orange-50 rounded-xl flex items-center justify-center mb-3">
                                <BookOpen className="w-5 h-5 text-[#f97316]" />
                            </div>
                            <div className="font-serif font-bold text-2xl text-[#0d0d0d]">{submissionsCount}</div>
                            <div className="text-xs text-[#6b7280] mt-0.5">Submissions</div>
                            <div className="text-xs text-[#9ca3af] mt-1">{solvedTitles.length} unique problems</div>
                        </div>
                    </Link>

                    {/* Strengths ‚Äî expandable */}
                    <ExpandableCard
                        icon={Star}
                        color="text-yellow-500"
                        bg="bg-yellow-50"
                        count={strengths.length}
                        label="Strengths"
                        sublabel="click to expand"
                        items={strengths}
                        emptyText="Solve more problems to surface your strengths."
                        badgeClass="bg-yellow-50 text-yellow-700 border border-yellow-200"
                    />

                    {/* Gaps ‚Äî expandable */}
                    <ExpandableCard
                        icon={AlertTriangle}
                        color="text-red-500"
                        bg="bg-red-50"
                        count={gaps.length}
                        label="Concept Gaps"
                        sublabel="click to expand"
                        items={gaps}
                        emptyText="No gaps identified yet ‚Äî keep submitting!"
                        badgeClass="bg-red-50 text-red-600 border border-red-200"
                    />

                    {/* Topics to revise ‚Äî expandable */}
                    <ExpandableCard
                        icon={RefreshCw}
                        color="text-indigo-500"
                        bg="bg-indigo-50"
                        count={toRevise.length}
                        label="To Revise"
                        sublabel="click to expand"
                        items={toRevise}
                        emptyText="Nothing flagged for revision yet."
                        badgeClass="bg-indigo-50 text-indigo-700 border border-indigo-200"
                    />
                </motion.div>

                {/* ‚îÄ‚îÄ KNOWN CONCEPTS STRIP ‚îÄ‚îÄ */}
                {knownConcepts.length > 0 && (
                    <motion.div
                        className="bg-white border border-[#e5e7eb] rounded-2xl p-5 mb-8"
                        initial={{ opacity: 0, y: 14 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.5, delay: 0.15 }}
                    >
                        <div className="flex items-center gap-2 mb-3">
                            <BarChart2 className="w-4 h-4 text-[#f97316]" />
                            <span className="text-xs font-bold uppercase tracking-widest text-[#9ca3af]">
                                Known Concepts ‚Äî {knownConcepts.length}
                            </span>
                        </div>
                        <div className="flex flex-wrap gap-2">
                            {knownConcepts.map((c: string) => {
                                const score = profile?.skills?.[c] ?? 0;
                                const isStrength = strengths.includes(c);
                                const isGap = gaps.includes(c);
                                const cls = isGap
                                    ? "bg-red-50 text-red-600 border border-red-200"
                                    : isStrength
                                        ? "bg-yellow-50 text-yellow-700 border border-yellow-200"
                                        : score >= 0.7
                                            ? "bg-green-50 text-green-700 border border-green-200"
                                            : "bg-[#f3f4f6] text-[#6b7280] border border-[#e5e7eb]";
                                return (
                                    <span key={c} className={`text-xs font-medium px-2.5 py-1 rounded-full ${cls}`}>
                                        {c}
                                        {score > 0 && <span className="ml-1 opacity-60">{Math.round(score * 100)}%</span>}
                                    </span>
                                );
                            })}
                        </div>
                    </motion.div>
                )}

                {/* ‚îÄ‚îÄ MAIN GRID: Solved + Upcoming ‚îÄ‚îÄ */}
                <div className="grid lg:grid-cols-2 gap-6">

                    {/* Solved problems */}
                    <motion.div
                        className="bg-white border border-[#e5e7eb] rounded-2xl p-6"
                        initial={{ opacity: 0, x: -20 }}
                        animate={{ opacity: 1, x: 0 }}
                        transition={{ duration: 0.5, delay: 0.2 }}
                    >
                        <div className="flex items-center justify-between mb-5">
                            <div className="flex items-center gap-2">
                                <Clock className="w-4 h-4 text-[#f97316]" />
                                <h2 className="text-xs font-bold uppercase tracking-widest text-[#9ca3af]">
                                    Recently Solved
                                </h2>
                            </div>
                            <Link href="/profile" className="text-xs text-[#f97316] font-medium no-underline hover:underline flex items-center gap-1">
                                Full history <ArrowRight className="w-3 h-3" />
                            </Link>
                        </div>

                        {solvedHistory.length === 0 ? (
                            <div className="text-center py-8">
                                <BookOpen className="w-8 h-8 text-[#e5e7eb] mx-auto mb-3" />
                                <p className="text-sm text-[#9ca3af]">No submissions yet.</p>
                                <Link href="/problems" className="text-xs text-[#f97316] font-medium no-underline hover:underline mt-1 inline-block">
                                    Solve your first problem ‚Üí
                                </Link>
                            </div>
                        ) : (
                            <div className="flex flex-col gap-1">
                                {solvedHistory.slice(0, 8).map((r: any, i: number) => (
                                    <Link
                                        key={i}
                                        href={`/problems/${r.problem_id}`}
                                        className="flex items-center justify-between px-4 py-3 rounded-xl hover:bg-[#f9fafb] transition-colors no-underline group"
                                    >
                                        <div>
                                            <p className="text-sm font-medium text-[#374151] group-hover:text-[#0d0d0d] transition-colors">
                                                {r.problem_title || r.problem_id}
                                            </p>
                                            {r.created_at && (
                                                <p className="text-xs text-[#9ca3af]">
                                                    {new Date(r.created_at).toLocaleDateString("en-IN", { day: "numeric", month: "short" })}
                                                </p>
                                            )}
                                        </div>
                                        <div className="flex items-center gap-2">
                                            {r.score !== undefined && (
                                                <span className={`text-xs font-bold px-2 py-0.5 rounded-full ${r.score >= 70 ? "bg-green-50 text-green-600" :
                                                    r.score >= 40 ? "bg-yellow-50 text-yellow-600" :
                                                        "bg-red-50 text-red-500"
                                                    }`}>
                                                    {r.score}/100
                                                </span>
                                            )}
                                            <ArrowRight className="w-3.5 h-3.5 text-[#d1d5db] group-hover:text-[#f97316] transition-colors" />
                                        </div>
                                    </Link>
                                ))}
                            </div>
                        )}
                    </motion.div>

                    {/* Upcoming / Recommended problems */}
                    <motion.div
                        className="bg-white border border-[#e5e7eb] rounded-2xl p-6"
                        initial={{ opacity: 0, x: 20 }}
                        animate={{ opacity: 1, x: 0 }}
                        transition={{ duration: 0.5, delay: 0.2 }}
                    >
                        <div className="flex items-center justify-between mb-5">
                            <div className="flex items-center gap-2">
                                <Zap className="w-4 h-4 text-[#f97316]" />
                                <h2 className="text-xs font-bold uppercase tracking-widest text-[#9ca3af]">
                                    Up Next
                                </h2>
                            </div>
                            <div className="flex items-center gap-3">
                                {recommendations.length > 0 && (
                                    <span className="text-xs text-[#9ca3af] hidden sm:block">
                                        Based on your knowledge graph
                                    </span>
                                )}
                                <Link href="/problems" className="text-xs text-[#f97316] font-medium no-underline hover:underline flex items-center gap-1">
                                    Browse all <ArrowRight className="w-3 h-3" />
                                </Link>
                            </div>
                        </div>

                        {/* Recommended concepts from Neo4j */}
                        {recommendations.length > 0 && (
                            <div className="flex flex-wrap gap-2 mb-4">
                                {recommendations.map((r: any) => (
                                    <span key={r.id || r.name} className="text-xs bg-[#fff7ed] text-[#f97316] border border-[#fed7aa] px-2.5 py-1 rounded-full flex items-center gap-1">
                                        <GitBranch className="w-3 h-3" /> {r.name}
                                    </span>
                                ))}
                            </div>
                        )}

                        {upcomingProblems.length === 0 ? (
                            <div className="text-center py-8">
                                <Sparkles className="w-8 h-8 text-[#e5e7eb] mx-auto mb-3" />
                                <p className="text-sm text-[#9ca3af]">Complete onboarding to get recommendations.</p>
                            </div>
                        ) : (
                            <div className="flex flex-col gap-1">
                                {upcomingProblems.map((p: any) => (
                                    <Link
                                        key={p.id}
                                        href={`/problems/${p.id}`}
                                        className="flex items-center justify-between px-4 py-3 rounded-xl hover:bg-[#f9fafb] transition-colors no-underline group"
                                    >
                                        <div>
                                            <p className="text-sm font-medium text-[#374151] group-hover:text-[#0d0d0d] transition-colors">
                                                {p.title}
                                            </p>
                                            {p.concept_ids?.length > 0 && (
                                                <p className="text-xs text-[#9ca3af]">{p.concept_ids.slice(0, 2).join(", ")}</p>
                                            )}
                                        </div>
                                        <div className="flex items-center gap-2">
                                            {p.difficulty && (
                                                <span className={`text-xs font-semibold px-2 py-0.5 rounded-full ${DIFF_BG[p.difficulty]} ${DIFF_COLORS[p.difficulty]}`}>
                                                    {DIFF_LABELS[p.difficulty]}
                                                </span>
                                            )}
                                            <ArrowRight className="w-3.5 h-3.5 text-[#d1d5db] group-hover:text-[#f97316] transition-colors" />
                                        </div>
                                    </Link>
                                ))}
                            </div>
                        )}
                    </motion.div>
                </div>

                {/* ‚îÄ‚îÄ QUICK LINKS ‚îÄ‚îÄ */}
                <motion.div
                    className="grid grid-cols-2 md:grid-cols-4 gap-4 mt-6"
                    initial={{ opacity: 0, y: 18 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.5, delay: 0.35 }}
                >
                    {[
                        { href: "/problems", icon: BookOpen, label: "Problems", sub: "50+ DSA problems" },
                        { href: "/graph", icon: GitBranch, label: "Knowledge Graph", sub: "30 concepts mapped" },
                        { href: "/profile", icon: BarChart2, label: "My Profile", sub: "Skills & history" },
                        { href: "/problems", icon: Brain, label: "Ask AI Tutor", sub: "On any problem" },
                    ].map((item, i) => (
                        <Link
                            key={i}
                            href={item.href}
                            className="bg-white border border-[#e5e7eb] rounded-xl p-5 card-hover no-underline group"
                        >
                            <item.icon className="w-5 h-5 text-[#f97316] mb-3 group-hover:scale-110 transition-transform" />
                            <p className="font-semibold text-sm text-[#0d0d0d]">{item.label}</p>
                            <p className="text-xs text-[#9ca3af] mt-0.5">{item.sub}</p>
                        </Link>
                    ))}
                </motion.div>

            </div>
        </div>
    );
}
